import React, {Component} from 'react'
import HeaderInterno from './HeaderInterno'
import base from './base'
import {Redirect} from 'react-router-dom'

class NovoAnuncio extends Component{
    constructor(props){
        super(props)
        this.state ={
            sucess: false
        }
        this.handleSubmit = this.handleSubmit.bind(this)
    }
    handleSubmit(e){
        const NovoAnuncio = {
            nome: this.nome.value,
            descricao: this.descricao.value,
            preco: this.preco.value,
            telefone: this.telefone.value,
            categoria: this.categoria.value,
            vendedor: this.vendedor.value
        }
        base.push('anuncios', {
            data: NovoAnuncio
        })
        .then(() => {
            this.setState({sucess: true})
        })

        e.preventDefault()
    }
    render(){
        return(
            <div>
            { this.state.sucess && <Redirect to="/" /> }
                <HeaderInterno />
                <div className="container" style={{paddingTop:'240px'}}>
                    <h1>Novo Anuncio</h1>
                    <form onSubmit={this.handleSubmit}>
                        <div className='form-group'>
                            <label htmlFor='nome'>Nome</label>
                            <input type='text' className="form-control" id="nome" placeholder="Nome" required ref={(ref) => this.nome = ref}/>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='nome'>Categorias</label>
                            <select ref={(ref) => this.categoria = ref}>
                                {this.props.categorias.map(cat => <option key={cat.url} value={cat.url}>{cat.categoria}</option>)}
                            </select>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='descricao'>Descrição</label>
                            <input type='text' className="form-control" id="descricao" placeholder="Descrição" required ref={(ref) => this.descricao = ref}/>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='preco'>Preço</label>
                            <input type='text' className="form-control" id="preco" placeholder="Preço" required ref={(ref) => this.preco = ref}/>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='telefone'>Telefone</label>
                            <input type='text' className="form-control" id="telefone" placeholder="Telefone" required ref={(ref) => this.telefone = ref}/>
                        </div>
                        <div className='form-group'>
                            <label htmlFor='vendedor'>Vendedor</label>
                            <input type='text' className="form-control" id="vendedor" placeholder="Vendedor" required ref={(ref) => this.vendedor = ref}/>
                        </div>
                        <button type="submit" className="btn btn-primary">Salvar anúncio</button>
                    </form>
                </div>
            </div>
        )
    }
}

export default NovoAnuncio


