import React from 'react'
import {Link} from 'react-router-dom'

const LinkCategoria = ({categoria}) =>{
    return(
        <Link to={`/categorias/${categoria.url}`} className="btn category h-100 m-2 col-sm" style={{color:'#006F83'}}>
            <i className={`fa ${categoria.icon}`} aria-hidden="true" style={{fontSize:'100px', color:'#006F83'}}></i><br />
            {categoria.categoria}
        </Link>
    )
}

export default LinkCategoria


