import React from 'react'

const AnuncioHome = ({anuncio}) =>{
    return(
        <div className="col-lg-6 col-md-6 mb-6" style={{marginBottom:'30px'}}>
        <div className="anuncio h-100">
          <span className="label label-info">{anuncio.categoria}</span>
          <div className="card-body">
            <h4 className="card-title">
              <a to={`/anuncios/ver/`}>{anuncio.nome}</a>
            </h4>
            <h5>{anuncio.preco}</h5>
            <p className="card-text">{anuncio.descricao}</p>
          </div>
        </div>
        </div>
    )
}

export default AnuncioHome


