import React from 'react'
import logo from './logo.png'
import {Link} from 'react-router-dom'

const HeaderHome = (props) => {
    return(
        <div className="jumbotron">
            <div className="container">
                <div className="row">
                    <div className="col-lg-6">
                        <h1 className="display-3 text-left"><img src={logo} className="App-logo" alt="logo" width="200" /></h1>
                    </div>
                    <div className="col-lg-6">
                        <p className="text-right" style={{margin:'20px 0'}}><Link className="btn btn-anuncie btn-lg" to="/novo-anuncio" role="button">Anuncie Grátis &raquo;</Link></p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default HeaderHome


